// 日期格式化
let DateFormat = function (date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str))
    }
  }
  return fmt
}

function padLeftZero (str) {
  return ('00' + str).substr(str.length)
}

// 窗口时间判断
let setCorrectSQDate = function (date, type) {
  var dateStartDate = new Date(date)
  var month = dateStartDate.getMonth() + 1
  if (month < 10) {
    month = '0' + month
  }
  var day = dateStartDate.getDate()
  if (day < 10) {
    day = '0' + day
  }
  var startDate = dateStartDate.getFullYear() + '-' + month + '-' + day
  if (date === '') {
    return ''
  }
  var dateq = date.replace(/\//g, '')
  var dateStartTime = dateq.split(' ')[1]
  var dateStartDateTemp = new Date('2018/01/01 ' + dateStartTime)
  var hours = dateStartDateTemp.getHours() // 小时数
  var minute = dateStartDateTemp.getMinutes() // 分钟数
  if (hours < 9 || (hours === 9 && minute < 30)) {
    if (type === 'begin') {
      return startDate + ' 08:55:00'
    } else if (type === 'end') {
      return startDate + ' 09:30:00'
    }
  } else if (hours === 16 && (minute >= 30 && minute <= 59)) {
    if (type === 'begin') {
      return startDate + ' 16:30:00'
    } else if (type === 'end') {
      return startDate + ' ' + hours + ':' + minute + ':00'
    }
  } else if (hours >= 17) {
    if (type === 'begin') {
      return startDate + ' 16:30:00'
    } else if (type === 'end') {
      return startDate + ' 17:00:00'
    }
  } else {
    if (hours < 10) {
      hours = '0' + hours
    }
    if (minute < 10) {
      minute = '0' + minute
    }
    return startDate + ' ' + hours + ':' + minute + ':00'
  }
}

// 部门时间判断
let deptDate = function (dateValue, type) {
  let date = new Date(dateValue.replace(/-/g, '/'))
  let hour = date.getHours()
  let month = date.getMonth() + 1
  let day = date.getDate()
  let startDate = date.getFullYear() + '-' + month + '-' + day
  if (type === 'begin') {
    if (hour < 11) {
      startDate = startDate + ' 09:00:00'
    } else if (hour >= 11) {
      startDate = startDate + ' 12:00:00'
    }
  } else if (type === 'end') {
    if (hour < 11) {
      startDate = startDate + ' 12:00:00'
    } else if (hour >= 11) {
      startDate = startDate + ' 17:00:00'
    }
  }

  return startDate
}

// 日期比较 date1 > date2 返回true，否则返回 false
let compareDate = function (date1, date2) {
  let d1 = new Date(date1).getTime()
  let d2 = new Date(date2).getTime()
  if (d1 > d2) {
    return true
  } else {
    return false
  }
}

export { DateFormat, setCorrectSQDate, deptDate, compareDate }



