
import header from '@/components/header'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/vacation'
import { setCorrectSQDate, compareDate } from '@/utils/utils'

export default {
components: { 'v-header': header, Group, Datetime, PopupPicker },
created () {
Indicator.open({
text: '正在加载...',
spinnerType: 'fading-circle'
})
API.GET_VACATION_TYPE(this, {}).then((rep) => {
Indicator.close()
let result = rep.body
if (result.success) {
this.list = result.data.map((item) => {
item.name = item.aaa103
item.value = item.aaa102
delete item.aaa103
delete item.aaa102
return item
})
this.vacationTypeList.push(this.list)
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
data () {
return {
headtitle: '请假申请',
isTypeOne: true,
formData: {
token: this.$store.getters.getUser.token,
zhgl_qj01001: this.$store.getters.getUser.data.name, // 姓名
zhgl_qj01003: '', // 职务
zhgl_qj01002: this.$store.getters.getUser.data.orgname, // 部门名
zhgl_qj01004: '', // 请假类型
zhgl_qj01005: '', // 请假原因
zhgl_qj01006: '', // 请假开始时间
zhgl_qj01007: '', // 请假结束时间
zhgl_qj01009: '', // 请假人电话
zhgl_qj01016: '', // 岗位代理人
zhgl_qj01027: '01', // 01 窗口人员    02 审批局(传固定值)
zhgl_qj01028: '', // 地点
zhgl_qj01030: '' 
},
vacationTypeList: [], // 请假类型
vacationvalue: [],
showName: true, // 下拉框选中显示name
list: [] 
}
},
methods: {
setStartDate: function (dateTime) {
this.formData.zhgl_qj01006 = ''
this.formData.zhgl_qj01007 = ''
Indicator.open({
text: '正在检查日期...',
spinnerType: 'fading-circle'
})
let time = dateTime.substring(0, 10)
API.IS_WORK_DAY(this, { time }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.data === 1) {
this.formData.zhgl_qj01006 = setCorrectSQDate(dateTime, 'begin')
if (this.formData.zhgl_qj01006 && this.formData.zhgl_qj01007) {
this.setHours()
}
} else {
MessageBox.alert('请选择工作日！', '操作提示')
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
setEndDate: function (dateTime) {
if (!compareDate(dateTime, this.formData.zhgl_qj01006)) {
this.formData.zhgl_qj01007 = ''
MessageBox.alert('结束时间不能小于开始时间', '操作提示')
return false
}
Indicator.open({
text: '正在检查日期...',
spinnerType: 'fading-circle'
})
let time = dateTime.substring(0, 10)
API.IS_WORK_DAY(this, { time }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.data === 1) {
this.formData.zhgl_qj01007 = setCorrectSQDate(dateTime, 'end')
this.setHours()
} else {
MessageBox.alert('请选择工作日！', '操作提示')
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
setHours: function () {
Indicator.open({
text: '正在设置请假工时...',
spinnerType: 'fading-circle'
})
API.WINDOW_GET_HOURS(this, { zhgl_qj01006: this.formData.zhgl_qj01006, zhgl_qj01007: this.formData.zhgl_qj01007 }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
this.formData.zhgl_qj01008 = this.formData.zhgl_qj01030 = result.data + '小时'
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
reset: function () {
this.formData = {
zhgl_qj01001: this.$store.getters.getUser.data.name, // 姓名
zhgl_qj01003: '', // 职务
zhgl_qj01002: this.$store.getters.getUser.data.orgname, // 部门名
zhgl_qj01004: '', // 请假类型
zhgl_qj01005: '', // 请假原因
zhgl_qj01006: '', // 请假开始时间
zhgl_qj01007: '', // 请假结束时间
zhgl_qj01009: '', // 请假人电话
zhgl_qj01016: '', // 岗位代理人
zhgl_qj01027: '01', // 01 窗口人员    02 审批局(传固定值)
zhgl_qj01028: '', // 地点
zhgl_qj01030: '' 
}
},
confirm: function () {
this.formData.zhgl_qj01027 = '01'
this.formData.zhgl_qj01028 = '0'
this.formData.zhgl_qj01004 = this.vacationvalue[0]
if (this.validateForm()) {
Indicator.open({
text: '正在提交...',
spinnerType: 'fading-circle'
})
this.formData.zhgl_qj01008 = this.formData.zhgl_qj01008.substring(0, this.formData.zhgl_qj01008.length - 2)
this.formData.zhgl_qj01030 = this.formData.zhgl_qj01030.substring(0, this.formData.zhgl_qj01030.length - 2)
this.windowConfirm()
} else {
MessageBox.alert('请正确填写内容！', '操作提示')
}
},
validateForm: function () {
let isPass = true
for (let key in this.formData) {
if (this.formData[key] === '') {
isPass = false
}
}

if (!this.isMobileNo(this.formData.zhgl_qj01009)) {
isPass = false
}

return isPass
},
isMobileNo: function (value) {
var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
if (reg.test(value)) {
return true
}
return false
},
windowConfirm: function () {
API.WINDOW_APPLY_VACATION(this, this.formData).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.state) {
MessageBox.alert('申请成功！2s后返回上一页', '操作提示')
setTimeout(() => {
this.$router.go(-1)
}, 2000)
} else {
MessageBox.alert('申请失败！' + result.message, '操作提示')
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
}
}
}




